import React from "react";
import { Draggable } from "react-beautiful-dnd";
import styled from "styled-components";

/**
 * An important nuance: in order for the element not to twitch when dragged, the key role is played by the styles of the element. I * had a problem with this, which is solved in passing: each element of the list should have only a margin-bottom, and the boundary * of the element should be present before dragging, because when we add the border element, when you drag the border disappears,  * that leads to not beautiful drop.
 */

const Container = styled.div`
  width: 250px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
  margin-bottom: 5px;
  border-radius: 10px;
  color: ${(props) => {
    if (props.author === "Spiderman" && props.isDragging) {
      return "#000";
    }

    if (props.author === "Batman" && props.isDragging) {
      return "#fff";
    }
  }};
  transition: background 0.5s ease;
  background: ${(props) =>
    props.isDragging ? props.theme : "rgba(231, 240, 255, 0.57)"};
  border: ${(props) =>
    props.isDragging
      ? "1px solid green"
      : "1px solid rgba(231, 240, 255, 0.57)"};

  margin-right: ${props => props.row ? '10px' : '0'};
`;

const Img = styled.img`
  padding: 10px;
  width: 50px;
  height: 50px;
`;

const ContentBlock = styled.div`
  display: flex;
  flex-grow: 2;
  flex-direction: column;
  justify-content: space-between;
`;

const Content = styled.div`
  padding: 10px 0;
`;

const Author = styled.div`
  width: fit-content;
  display: flex;
  text-align: center;
  border-radius: 5px;
  padding: 5px;
  color: ${(props) => {
    if (props.author === "Batman" && !props.isDragging) {
      return "#fff";
    }
  }};
  background: ${(props) => props.theme};
`;

const Card = ({ author, content, img, id, theme, index, row }) => {
  return (
    <Draggable draggableId={id} index={index}>
      {(provided, snapshot) => (
        <Container
          ref={provided.innerRef}
          {...provided.dragHandleProps}
          {...provided.draggableProps}
          isDragging={snapshot.isDragging}
          theme={theme}
          author={author}
          row
        >
          <Img src={img} />
          <ContentBlock>
            <Content>{content}</Content>
            <Author
              theme={theme}
              isDragging={snapshot.isDragging}
              author={author}
            >
              {author}
            </Author>
          </ContentBlock>
        </Container>
      )}
    </Draggable>
  );
};

export default Card;
