import React, { useRef, useState } from "react";
import Card from "./Card";
import styled, { css } from "styled-components";
import { Draggable, Droppable } from "react-beautiful-dnd";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Container = styled.div`
  position: relative;
  box-sizing: border-box;
  margin: 0 10px;
  border: 3px solid rgba(210, 210, 209, 1);
  box-shadow: 8px 8px 15px rgba(209, 207, 192, 0.51);
  display: flex;
  flex-direction: column;
`;

export const Title = styled.div`
  background: ${(props) =>
    props.isDragging ? "rgba(129, 226, 74, 1)" : "rgba(6, 194, 0, 0.47)"};
  font-size: 20px;
  text-align: center;
  border-bottom: 1px solid lightgray;
  padding: 5px;
`;

export const CardsList = styled.div`
  padding: 5px 5px;
  flex-grow: 2;
  min-height: 100px;
  transition: background-color 0.5s ease;
  background-color: ${(props) =>
    props.isDraggingOver
      ? "rgba(112, 200, 62, 1)"
      : "rgba(125, 185, 123, 0.77)"};

  ${props => props.row && css`
   display: flex; 
   justify-content: space-between;
  `}
`;

export const AddButtonIcon = styled.i`
  cursor: pointer;
  position: absolute;
  bottom: -40px;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 28px;
  color: white;
  transition: all 0.3s ease;

  &: hover {
    font-size: 32px;
  }
`;

export const NewMessageInput = styled.input`
  position: absolute;
  bottom: -65px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 250px;
  height: 25px;
  border-radius: 5px;
  border: none;
  opacity: ${(props) => (props.visible ? "1" : "0")};
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border: 1px solid green;
  }
`;

const Column = ({ title, cardsIds, id, state, index, addCard }) => {
  const [newHeroesMessage, setNewHeroesMessage] = useState("");
  const [showMessageInput, setShowMessageInput] = useState(false);

  const cardImg = useRef();
  const cardTheme = useRef();
  const inputForMessage = useRef(null);

  const { cards } = state;

  const addNewMessage = (e) => {
    const content = e.target.value;
    setNewHeroesMessage(content);
  };

  const addNewMessageCard = (id) => {
    if (!newHeroesMessage) return;

    const cards = state.cards;
    const hero = title.toLowerCase();
    let numberOfCards = 0;

    for (let card in cards) {
      if (card.includes(hero)) {
        ++numberOfCards;
      }
    }

    const newCardName = `${hero}-${numberOfCards + 1}`;

    const newCard = {
      [`${hero}-${numberOfCards + 1}`]: {
        id: newCardName,
        author: title,
        content: `“${newHeroesMessage}”`,
        img: cardImg.current,
        themeColor: cardTheme.current,
      },
    };

    const prevColumn = state.columns[id];

    const newColumn = {
      [id]: {
        ...prevColumn,
        cardsIds: [...prevColumn.cardsIds, newCardName],
      },
    };

    addCard(newCard, newColumn);
    setNewHeroesMessage("");
  };

  return (
    <Draggable draggableId={id} index={index}>
      {(provided, snapshot) => (
        <Container
          {...provided.draggableProps}
          ref={provided.innerRef}
          isDragging={snapshot.isDragging}
          id={id}
        >
          <Title {...provided.dragHandleProps} isDragging={snapshot.isDragging}>
            {title}
          </Title>
          <Droppable droppableId={id} type="card">
            {(provided, snapshot) => (
              <CardsList
                ref={provided.innerRef}
                {...provided.droppableProps}
                isDraggingOver={snapshot.isDraggingOver}
              >
                {cardsIds.map((cardId, index) => {
                  const card = cards[cardId];
                  if (cardImg.current !== card.img) {
                    cardImg.current = card.img;
                  }

                  if (cardTheme.current !== card.themeColor) {
                    cardTheme.current = card.themeColor;
                  }
                  return (
                    <Card
                      key={card.id}
                      index={index}
                      author={card.author}
                      content={card.content}
                      img={card.img}
                      id={card.id}
                      theme={card.themeColor}
                    />
                  );
                })}
                {provided.placeholder}
                <AddButtonIcon
                  onClick={() => {
                    inputForMessage.current.focus();
                    addNewMessageCard(id);
                    setShowMessageInput((prevState) => !prevState);
                  }}
                >
                  <FontAwesomeIcon icon={["fas", "plus-square"]} />
                </AddButtonIcon>
                <NewMessageInput
                  placeholder="Enter your message for this hero..."
                  value={newHeroesMessage}
                  onChange={addNewMessage}
                  ref={inputForMessage}
                  visible={showMessageInput}
                  type="text"
                />
              </CardsList>
            )}
          </Droppable>
        </Container>
      )}
    </Draggable>
  );
};

export default Column;
