import React, { useState } from "react";
import { DragDropContext, Droppable } from "react-beautiful-dnd";
import styled, { css } from "styled-components";

import Column from "./Column";
import initialData from "./initialData";
import Row from "./Row";

const Container = styled.div`
  display: flex;
  transition: all 1s easy;

  ${props => props.secondContainer && css`
    margin: 100px 0;
  `}
`;

const App = () => {
  const [state, setState] = useState(initialData);

  const addCard = (card, column) => {
    const newState = {
      ...state,
      cards: {
        ...state.cards,
        ...card,
      },
      columns: {
        ...state.columns,
        ...column,
      },
    };

    setState(newState);
  };

  const onDragEnd = (event) => {    
    const { source, destination, draggableId, type } = event;

    if (!destination) return;

    if (
      source.index === destination.index &&
      source.droppableId === destination.droppableId
    )
      return;

    if (type === "column") {
      const newOrderColumns = Array.from(state.orderColumns);
      newOrderColumns.splice(source.index, 1);
      newOrderColumns.splice(destination.index, 0, draggableId);

      const newState = {
        ...state,
        orderColumns: newOrderColumns,
      };

      setState(newState);
      return;
    }

    const start = state.columns[source.droppableId];
    const finish = state.columns[destination.droppableId];

    if (start === finish) {
      const newCardsIds = Array.from(
        state.columns[source.droppableId].cardsIds
      );
      newCardsIds.splice(source.index, 1);
      newCardsIds.splice(destination.index, 0, draggableId);

      const newColumns = {
        ...state.columns,
        [destination.droppableId]: {
          ...start,
          cardsIds: newCardsIds,
        },
      };

      const newState = {
        ...state,
        columns: { ...newColumns },
      };

      setState(newState);
      return;
    }

    const newStartIds = Array.from(state.columns[source.droppableId].cardsIds);
    const newFinishIds = Array.from(
      state.columns[destination.droppableId].cardsIds
    );

    newStartIds.splice(source.index, 1);
    newFinishIds.splice(destination.index, 0, draggableId);

    const newStartColumn = {
      ...state.columns[source.droppableId],
      cardsIds: newStartIds,
    };
    const newFinishColumn = {
      ...state.columns[destination.droppableId],
      cardsIds: newFinishIds,
    };

    const newState = {
      ...state,
      columns: {
        ...state.columns,
        [source.droppableId]: { ...newStartColumn },
        [destination.droppableId]: { ...newFinishColumn },
      },
    };

    setState(newState);
  };

  return (
    <>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable
          droppableId="all-columns"
          direction="horizontal"
          type="column"
        >
          {(provided) => (
            <Container {...provided.droppableProps} ref={provided.innerRef}>
              {state.orderColumns.map((columNumber, index) => {
                const column = state.columns[columNumber];
                return (
                  <Column
                    key={column.id}
                    id={column.id}
                    title={column.title}
                    cardsIds={column.cardsIds}
                    state={state}
                    index={index}
                    addCard={addCard}
                  />
                );
              })}
              {provided.placeholder}
            </Container>
          )}
        </Droppable>
      </DragDropContext>
      <DragDropContext onDragEnd={onDragEnd}>
          <Container secondContainer={true}>
            {state.orderRows.map((columNumber, index) => {
              const row = state.columns[columNumber];
              return (
                <Row
                  key={row.id}
                  id={row.id}
                  cardsIds={row.cardsIds}
                  index={index}
                  state={state}
                />
              );
            })}
          </Container>
      </DragDropContext>
    </>
  );
};

export default App;
