import React, { useRef } from "react";
import { Droppable } from "react-beautiful-dnd";
import styled from "styled-components";
import Card from "./Card";
import { CardsList, Title } from "./Column";

const Container = styled.div`
  position: relative;
  box-sizing: border-box;
  margin: 0 10px;
  border: 3px solid rgba(210, 210, 209, 1);
  box-shadow: 8px 8px 15px rgba(209, 207, 192, 0.51);
  display: flex;
  flex-direction: column;
`;

const Row = ({ cardsIds, id, index, state }) => {
  const { cards } = state;

  const cardImg = useRef();
  const cardTheme = useRef();

  return (
    <Container>
      <Title>Horizontal List</Title>
      <Droppable droppableId={id} direction="horizontal">
        {(provided, snapshot) => (
          <CardsList
            ref={provided.innerRef}
            {...provided.droppableProps}
            isDraggingOver={snapshot.isDraggingOver}
			row={true}
          >
            {cardsIds.map((cardId, index) => {
              const card = cards[cardId];
              if (cardImg.current !== card.img) {
                cardImg.current = card.img;
              }

              if (cardTheme.current !== card.themeColor) {
                cardTheme.current = card.themeColor;
              }
              return (
                <Card
                  key={card.id}
                  index={index}
                  author={card.author}
                  content={card.content}
                  img={card.img}
                  id={card.id}
                  theme={card.themeColor}
				  row={true}
                />
              );
            })}
            {provided.placeholder}
          </CardsList>
        )}
      </Droppable>
    </Container>
  );
};

export default Row;
